import { fetchPokemonsByNames } from '@/store/slices/entities/pokemon';
import { PokemonRecord } from '@/types/entities/pokemon-record.interface';
import { useEffect, useRef } from 'react';
import { useAppDispatch } from './store';

interface RecordsByName {
  [key: string]: PokemonRecord;
}

export const usePokemonDetailsData = (records: PokemonRecord[]) => {
  const dispatch = useAppDispatch();
  const prevRecordsByName = useRef(<RecordsByName>{});

  useEffect(() => {
    if (records.length) {
      const recordsToFetch = records.filter(({ name }) => !prevRecordsByName.current[name]);

      dispatch(fetchPokemonsByNames(recordsToFetch.map(({ name }) => name)));

      prevRecordsByName.current = {
        ...prevRecordsByName.current,
        ...records.reduce((acc, rec) => {
          acc[rec.name] = rec;
          return acc;
        }, <RecordsByName>{}),
      };
    }
  }, [records.length]);
};
