import React from 'react';
import { useAppSelector } from '@/hooks/store';
import { RootState } from '@/store';
import {
  selectPokemonByName,
  selectPokemonErrorByName,
  selectPokemonIsLoadingByName,
} from '@/store/slices/entities/pokemon';
import pokeballImg from '@/assets/img/pokeball.png';
import './style.scss';
import { Pokemon } from '@/types/entities/pokemon.interface';

interface PokemonCardProps {
  name: string;
}

export const PokemonCard = React.forwardRef<HTMLElement, PokemonCardProps>(
  ({ name }, forwardedRef) => {
    const pokemon: Pokemon | null = useAppSelector((state: RootState) =>
      selectPokemonByName(state, name)
    );
    const isLoading = useAppSelector((state: RootState) =>
      selectPokemonIsLoadingByName(state, name)
    );
    const error = useAppSelector((state: RootState) => selectPokemonErrorByName(state, name));

    return (
      <article
        className="pokemon-card"
        ref={forwardedRef}
        data-pokemon-name={name}
        data-hasref={!!forwardedRef}
      >
        <main className="pokemon-card__body">
          <div className="pokemon-card__image-wrapper">
            <img
              src={getImageUrl(pokemon, isLoading)}
              alt="Pokemon"
              className={getImageClass(isLoading)}
            />
          </div>
        </main>
        <footer className="pokemon-card__footer">
          <h4 className="pokemon-card__title">{getTitle({ error, isLoading, pokemon })}</h4>
        </footer>
      </article>
    );
  }
);

function getTitle(params: { error: string; isLoading: boolean; pokemon: Pokemon | null }) {
  const { error, isLoading, pokemon } = params;
  if (isLoading) return 'Loading...';
  if (error) return error;

  return pokemon?.prettyName || 'N/A';
}

function getImageUrl(pokemon: Pokemon | null, isLoading: boolean) {
  if (isLoading) return pokeballImg;
  return pokemon?.imageUrl || pokeballImg;
}

function getImageClass(isLoading: boolean) {
  return `pokemon-card__image${isLoading ? ' pokemon-card__image--loading' : ''}`;
}
