import {
  fetchPokemonRecords,
  selectPokemonRecords,
  selectPokemonRecordsLoading,
  selectPokemonRecordsTotal,
} from '@/store/slices/entities/pokemon-record';
import { useCallback, useState } from 'react';
import { useAppDispatch, useAppSelector } from './store';

export const usePokemonRecordsData = (params: { limit: number }) => {
  const { limit } = params;
  const dispatch = useAppDispatch();
  const [hasFetchedInitial, setHasFetchedInitial] = useState(false);
  const records = useAppSelector(selectPokemonRecords);
  const total = useAppSelector(selectPokemonRecordsTotal);
  const isLoading = useAppSelector(selectPokemonRecordsLoading);

  const fetchInitial = useCallback(() => {
    if (hasFetchedInitial || records.length || isLoading) return;

    dispatch(fetchPokemonRecords({ offset: 0, limit }));
    setHasFetchedInitial(true);
  }, [dispatch, records, hasFetchedInitial, limit, isLoading]);

  const fetchMore = useCallback(() => {
    if (!hasFetchedInitial || isLoading || records.length === total) return;

    dispatch(fetchPokemonRecords({ offset: records.length, limit }));
  }, [dispatch, records, hasFetchedInitial, limit, total, isLoading]);

  return {
    fetchInitial,
    fetchMore,
    data: records,
  };
};
