import { createAsyncThunk, createSelector, createSlice } from '@reduxjs/toolkit';
import { getEntitiesByName, getUniqueIdentifiers } from '@/store/utils/mappers';
import { PokemonRecord } from '@/types/entities/pokemon-record.interface';
import type { RootState } from '@/store';
import PokemonService from '@/services/PokemonService/PokemonService';

interface PokemonRecordState {
  byName: { [name: string]: PokemonRecord };
  names: string[];
  error: string;
  isLoading: boolean;
  total: number;
}

const initialState: PokemonRecordState = {
  byName: {},
  names: [],
  error: '',
  isLoading: false,
  total: 0,
};

export const fetchPokemonRecords = createAsyncThunk(
  'pokemonRecord/fetchPokemonRecords',
  async (params: { offset: number; limit: number }) => {
    const { offset, limit } = params;
    return PokemonService.getRecords(offset, limit);
  }
);

export const pokemonRecordSlice = createSlice({
  name: 'pokemonRecord',
  initialState,
  reducers: {},
  extraReducers: (builder) => {
    builder
      .addCase(fetchPokemonRecords.pending, (state) => {
        state.error = '';
        state.isLoading = true;
      })
      .addCase(fetchPokemonRecords.fulfilled, (state, action) => {
        state.isLoading = false;

        const { total, data } = action.payload;
        const { byName, names } = getEntitiesByName(data);

        state.byName = {
          ...state.byName,
          ...byName,
        };
        state.names = getUniqueIdentifiers(state.names, names);

        state.total = total;
      })
      .addCase(fetchPokemonRecords.rejected, (state, action) => {
        state.error = `Error: Couldn't fetch'em all :(`;
        state.isLoading = false;
        console.error(action.payload);
      });
  },
});

export const selectPokemonRecords = createSelector(
  (state: RootState) => state.entities.pokemonRecord.names,
  (state: RootState) => state.entities.pokemonRecord.byName,
  (names, byName) => names.map((name) => byName[name])
);

export const selectPokemonRecordsTotal = (state: RootState) => state.entities.pokemonRecord.total;
export const selectPokemonRecordsLoading = (state: RootState) =>
  state.entities.pokemonRecord.isLoading;

export default pokemonRecordSlice.reducer;
