import { createAsyncThunk, createSelector, createSlice } from '@reduxjs/toolkit';
import { Pokemon } from '@/types/entities/pokemon.interface';
import { PokemonService } from '@/services/PokemonService';
import type { RootState } from '@/store';

interface PokemonState {
  byName: { [name: string]: Pokemon };
  errorByName: { [name: string]: string };
  isLoadingByName: { [name: string]: boolean };
}

const initialState: PokemonState = {
  byName: {},
  errorByName: {},
  isLoadingByName: {},
};

export const fetchPokemonsByNames = createAsyncThunk(
  'pokemon/fetchPokemonsByNames',
  async (names: string[]) => PokemonService.getBatchByNames(names)
);

export const pokemonSlice = createSlice({
  name: 'pokemon',
  initialState,
  reducers: {},
  extraReducers: (builder) => {
    builder
      .addCase(fetchPokemonsByNames.pending, (state, action) => {
        const names = action.meta.arg;

        names.forEach((name) => {
          state.isLoadingByName[name] = true;
          state.errorByName[name] = '';
        });
      })
      .addCase(fetchPokemonsByNames.fulfilled, (state, action) => {
        const { data: pokemons, failedNames } = action.payload;

        pokemons.forEach((pokemon) => {
          state.isLoadingByName[pokemon.name] = false;
          state.byName[pokemon.name] = pokemon;
        });
        mutateStateForFailedFetchesByNames(state, failedNames);
      })
      .addCase(fetchPokemonsByNames.rejected, (state, action) => {
        const names = action.meta.arg;

        mutateStateForFailedFetchesByNames(state, names);
      });
  },
});

export const selectPokemonByName = createSelector(
  (state: RootState) => state.entities.pokemon.byName,
  (state: RootState, name: string) => name,
  (byName, name) => byName[name] || null
);

export const selectPokemonIsLoadingByName = createSelector(
  (state: RootState) => state.entities.pokemon.isLoadingByName,
  (state: RootState, name: string) => name,
  (isLoadingByName, name) => isLoadingByName[name] ?? false
);

export const selectPokemonErrorByName = createSelector(
  (state: RootState) => state.entities.pokemon.errorByName,
  (state: RootState, name: string) => name,
  (errorByName, name) => errorByName[name] || ''
);

function getErrorMessageForName(name: string) {
  return `Couldn't fetch "${name}"`;
}

function mutateStateForFailedFetchesByNames(state: PokemonState, failedNames: string[]) {
  failedNames.forEach((name) => {
    state.isLoadingByName[name] = false;
    state.errorByName[name] = getErrorMessageForName(name);
  });
}

export default pokemonSlice.reducer;
