export const getEntitiesByName = <Entity extends { name: string }>(
  array: Entity[]
): {
  byName: { [key: string]: Entity };
  names: string[];
} =>
  array.reduce(
    (acc, entity) => {
      acc.byName[entity.name] = entity;
      acc.names.push(entity.name);
      return acc;
    },
    { byName: <{ [key: string]: Entity }>{}, names: <string[]>[] }
  );

export const getUniqueIdentifiers = <T>(baseArr: T[], newArr: T[]) =>
  Array.from(new Set([...baseArr, ...newArr]));
