import React, { useCallback, useEffect, useRef } from 'react';

interface UseInfiniteGridProps {
  onReachThreshold(): void;
  onReachEnd(): void;
}

export const useInfiniteGrid = ({ onReachThreshold, onReachEnd }: UseInfiniteGridProps) => {
  const thresholdObserverRef = useRef<IntersectionObserver | null>(null);
  const endObserverRef = useRef<IntersectionObserver | null>(null);

  const getObserverCallback =
    (intCb: () => void, observerRef: React.MutableRefObject<IntersectionObserver | null>) =>
    (element: HTMLElement) => {
      observerRef.current?.disconnect();

      if (element) {
        // eslint-disable-next-line no-param-reassign
        observerRef.current = new IntersectionObserver((elements) => {
          if (elements[0].isIntersecting) {
            intCb();
          }
        });

        observerRef.current.observe(element);
      }
    };
  const thresholdElementRef = useCallback(
    getObserverCallback(onReachThreshold, thresholdObserverRef),
    [onReachThreshold]
  );
  const lastElementRef = useCallback(getObserverCallback(onReachEnd, endObserverRef), [onReachEnd]);

  useEffect(() => {
    thresholdObserverRef.current?.disconnect();
    endObserverRef.current?.disconnect();
  }, []);

  return {
    thresholdElementRef,
    lastElementRef,
  };
};
