import { PokemonRecord } from '@/types/entities/pokemon-record.interface';
import { Pokemon as MappedPokemon } from '@/types/entities/pokemon.interface';
import { Pokemon, PokemonClient } from 'pokenode-ts';
import { PokemonFetchFailureException } from './exceptions/PokemonFetchFailureException';

class PokemonService {
  private client: PokemonClient;

  constructor(client: PokemonClient) {
    this.client = client;
  }

  public async getRecords(
    offset: number,
    limit: number
  ): Promise<{
    total: number;
    data: PokemonRecord[];
  }> {
    const { count, results } = await this.client.listPokemons(offset, limit);

    return {
      total: count,
      data: results.map((result) => ({ name: result.name })),
    };
  }

  public async getByName(name: string): Promise<MappedPokemon> {
    try {
      const pokemon = await this.client.getPokemonByName(name);

      return {
        id: pokemon.id,
        imageUrl: this.retrievePokemonImage(pokemon),
        name: pokemon.name,
        prettyName: this.formatName(pokemon.name),
        specie: pokemon.species.name,
      };
    } catch (error) {
      throw new PokemonFetchFailureException(`Couldn't fetch a pokemon with name "${name}"`, {
        name,
      });
    }
  }

  public async getBatchByNames(
    names: string[]
  ): Promise<{ data: MappedPokemon[]; failedNames: string[] }> {
    const responses = await Promise.allSettled(names.map((name) => this.getByName(name)));

    return responses.reduce(
      (acc, response) => {
        switch (response.status) {
          case 'fulfilled':
            acc.data.push(response.value);
            break;
          case 'rejected':
            if (response.reason instanceof PokemonFetchFailureException) {
              acc.failedNames.push(response.reason.data.name);
            }
            console.error(response.reason);
            break;
          default:
            return acc;
        }

        return acc;
      },
      { data: <MappedPokemon[]>[], failedNames: <string[]>[] }
    );
  }

  private formatName(kebabName: string) {
    return kebabName
      .split('-')
      .map((word) => `${word[0].toUpperCase()}${word.slice(1)}`)
      .join(' ');
  }

  private retrievePokemonImage(pokemon: Pokemon) {
    return pokemon.sprites.other?.dream_world.front_default || pokemon.sprites.front_default;
  }
}

export default new PokemonService(new PokemonClient());
