import React, { useEffect } from 'react';
import { usePokemonDetailsData } from '@/hooks/usePokemonDetailsData';
import { usePokemonRecordsData } from '@/hooks/usePokemonRecordsData';
import { useInfiniteGrid } from '@/hooks/useInfiniteGrid';
import { PokemonCard } from './components/PokemonCard';
import './style.scss';

interface PokemonGridProps {
  pageLimit?: number;
  prefetchThresholdFactor?: number;
}

export const PokemonGrid: React.FC<PokemonGridProps> = ({
  pageLimit = 20,
  prefetchThresholdFactor = 0.7,
}) => {
  const { fetchInitial, fetchMore, data: items } = usePokemonRecordsData({ limit: pageLimit });

  usePokemonDetailsData(items);

  const { thresholdElementRef, lastElementRef } = useInfiniteGrid({
    onReachThreshold: fetchMore,
    onReachEnd: fetchMore,
  });

  const getCardRef = (index: number, total: number) => {
    const maxIndex = total - 1;
    if (index === Math.round(maxIndex * prefetchThresholdFactor)) {
      return thresholdElementRef;
    }
    if (index === maxIndex) {
      return lastElementRef;
    }
    return null;
  };

  useEffect(() => {
    if (!items.length) {
      fetchInitial();
    }
  }, [items.length]);

  return (
    <div className="pokemon-grid">
      {items.map((item, i) => (
        <div className="pokemon-grid__cell" key={item.name}>
          <PokemonCard ref={getCardRef(i, items.length)} name={item.name} />
        </div>
      ))}
    </div>
  );
};
